.cardcontainer{
   padding:20px 100px;

    display: grid;
    grid-template-columns: auto auto auto ;
}
.Image{
    Width: 100%;
    Height: 40%;
    Border-top-left-radius: 5px;
    Border-top-right-radius : 5px;

}
.card{
    
    width: 300px;
    height: 400px;
    margin : 15px 10px 20px 50px;
    border-color: #0096ff;
    box-shadow: 5px 5px 5px grey;
   
    
}
.title{
font-size: 18px;
text-align: left;
Margin: 0;
margin-bottom: 8px;
font-weight: 600;
}
.author{
 color: #0096ff;
font-size: 14px;
font-style: Italic;
font-weight: 500;
}
.description{
font-Size: 14px;
font-weight: 400;
Line-height: 1.43;
color : rgba(0,0,0,.6);
Margin: 4px 0;
padding: 4px;
text-rendering: auto ;
margin-bottom: 15px;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  overflow: hidden;
  -webkit-box-orient: vertical;
  white-space: normal;
}
.date{
color: rgba(0,0,0,0.7);
font-size: 14px;
font-weight: 300;
} 