nav{
    border-bottom: 1px solid #fff;
    box-shadow: 2px 2px 2px gray; 
    background-color:white;
    padding: 10px;
    display: flex;
    justify-content: space-around;
    text-align: center;
    flex-wrap: wrap;
    

    
}
#leftside{
    display: flex;
    justify-content: space-between;
}
#logodiv{
 display: flex;
 flex-direction: column;
 
}
.logo{

font-size: 32px;
color: #0084ff;
font-weight: 900;
display: inline;


}
.logo-stories{
    line-height: 0;
text-align: left;
font-weight: 500;
font-size: 20px;
font-family:Raleway;
font-style: italic;
margin-top: 0px;
}
.drop-down-icon
{
    color: #000;
    margin-left:100px;
    margin-top: 10px;

}
#msg{
    width:300px;
    margin-right: 100px;
    display: inline-block;
    color:rgba(0,0,0,.6);
    opacity: .6;
    line-height: 1;
    padding-top:10px;
    font-weight: 600;
}
button{
    border-radius: 30px;
    padding:10px;
    color:white;
    background-color: #0084ff;
    border:none;
    cursor: pointer;

}